.tabs {
  display: flex;
  align-items: center;
  min-height: 655px;

  @include small-tablet {
    flex-direction: column;
  }

  @include mobile {
    min-height: 659px;
    width: 100%;
  }

  &__nav {
    flex-direction: column;
    border: 10px solid var(--color-blue-romance);
    border-right-width: 0;
    border-radius: 45px 0 0 45px;
    overflow: hidden;

    @include small-tablet {
      flex-direction: row-reverse;
      border-radius: 50px 50px 0 0;
      border-right-width: 10px;
      border-bottom-width: 0;
    }

    @include mobile {
      width: 100%;
      border-width: 15px;
      border-right-width: 15px;
    }
  }

  &__nav-item {
    @include mobile {
      flex-basis: 50%;
    }
  }

  &__nav-btn {
    width: 100%;
    padding: 21px 15px 20px;
    background-color: var(--color-white);
    font-weight: 400;
    font-size: 14px;
    line-height: 17px;
    color: var(--color-amulet);
    transition: color var(--ui-transition);

    @include hover {
      &:not(.tabs__nav-btn--active) {
        color: var(--color-pastel-green);

        .tabs__nav-icon {
          fill: var(--color-pastel-green);
        }
      }
    }

    &:active {
      background-color: var(--color-white);
      color: var(--color-amulet);
    }

    &:focus {
      background-color: var(--color-white);
      color: var(--color-amulet);
    }

    @include small-tablet {
      display: flex;
      align-items: center;
      height: 100%;
      padding: 28px 19px 31px;
    }

    @include mobile {
      justify-content: center;
    }

    &--active {
      box-shadow: inset 2px -2px 12px #226f215c;
      background-color: var(--color-green);
      color: var(--color-white);

      .tabs__nav-icon {
        fill: var(--color-white);
      }

      &:focus {
        background-color: var(--color-green);
        color: var(--color-white);
      }

      &:active {
        color: var(--color-white);
      }
    }
  }

  &__nav-icon {
    display: block;
    width: 33px;
    height: 33px;
    fill: var(--color-amulet);
    margin-bottom: 22px;
    transition: fill var(--ui-transition) ease;

    @include small-tablet {
      margin: 0;
      margin-right: 22px;
      display: none;
    }
  }

  &__nav-btn-desc {
    writing-mode: vertical-lr;
    transform: rotate(180deg);

    @include small-tablet {
      writing-mode: initial;
      transform: initial;
    }
  }

  &__content {
    width: 100%;
    max-width: 721px;
    background-color: var(--color-blue-romance);
    border-radius: 65px 65px;
    height: 100%;
    overflow: hidden;

    @include small-tablet {
      border-radius: 50px;
      max-width: 100%;
    }

    @include mobile {
      border-radius: 0 0 50px 50px;
      padding: 11px 15px 15px;
    }
  }

  &__panel {
    height: 100%;
    overflow: hidden;

    @include mobile {
      flex-direction: column;
      gap: 8px;
      overflow: initial;
    }

    &--active {
      display: flex;
      justify-content: space-between;
    }
  }

  &__content-list {
    margin: auto 0;
    margin-right: 20px;
    padding-left: 39px;
    padding-top: 20px;
    padding-bottom: 20px;

    @include tablet {
      padding-left: 23px;
      padding-top: 44px;
    }

    @include mobile {
      margin: 0;
      padding: 0;
      padding-left: 14px;
      max-width: 324px;
    }
  }

  &__content-item {
    padding-left: 50px;
    position: relative;

    @include mobile {
      padding-left: 46px;
    }

    &::before {
      @include pseudo;
      position: absolute;
      top: 0;
      left: 0;
      width: 34px;
      height: 34px;
      background-color: var(--color-white);
      border-radius: 10px;

      @include bg-auto;

      @include mobile {
        width: 30px;
        height: 30px
      }
    }

    @include mb(25px);

    @include tablet {
      @include mb(14px);
    }

    @include mobile {
      @include mb(18px);
    }
  }

  &__content-item--1 {
    &::before {
      background-image: url(../img/tabs-icon-1.svg);
    }
  }

  &__content-text {
    font-weight: 400;
    font-size: 20px;
    line-height: 30px;
    color: var(--color-gray-asparagus);

    @include tablet {
      font-size: 18px;
    }

    @include mobile {
      font-size: 16px;
      line-height: 26px;
    }
  }

  &__content-item--2 {
    &::before {
      background-image: url(../img/tabs-icon-2.svg);
    }
  }

  &__content-item--3 {
    &::before {
      background-image: url(../img/tabs-icon-3.svg);
    }
  }

  &__content-item--4 {
    &::before {
      background-image: url(../img/tabs-icon-4.svg);
    }
  }

  &__content-item--5 {
    &::before {
      background-image: url(../img/tabs-icon-5.svg);
    }
  }

  &__content-item--6 {
    &::before {
      background-image: url(../img/tabs-icon-6.svg);
    }
  }

  &__content-picture {
    overflow: hidden;
    border-radius: 65px;
    max-width: 285px;
    width: 100%;
    box-shadow: -4px 0px 8px #0f2c0f59;

    @include small-tablet {
      border-radius: 50px;
    }

    @include mobile {
      max-width: 100%;
      max-height: clamp(10.625rem, -5.6996rem + 69.6517vw, 19.375rem);
      box-shadow: 0px -4px 8px #0f2c0f59;
    }
  }

  &__content-image {
    height: 100%;
    width: 100%;

    &--position {
      object-position: -390px;
    }

    @include mobile {
      object-position: 0;
    }
  }

  &__content-item--7 {
    &::before {
      background-image: url(../img/tabs-icon-7.svg);
    }
  }

  &__content-item--8 {
    &::before {
      background-image: url(../img/tabs-icon-8.svg);
    }
  }

  &__content-item--9 {
    &::before {
      background-image: url(../img/tabs-icon-9.svg);
    }
  }

  &__content-item--10 {
    &::before {
      background-image: url(../img/tabs-icon-10.svg);
    }
  }

  &__content-item--11 {
    &::before {
      background-image: url(../img/tabs-icon-11.svg);
    }
  }

  &__content-item--12 {
    &::before {
      background-image: url(../img/tabs-icon-12.svg);
    }
  }
}
