.solution {
  padding: 95px 0 40px;
  position: relative;
  overflow: hidden;

  @include mobile {
    padding: 60px 0 40px;
  }

  &__decor-item {
    @include bg-contain;

    &--lung {
      width: 510px;
      height: 488px;
      background-image: url(../img/lung.svg);
      top: 150px;
      left: -237px;
      transform: translateX(-50px);
      opacity: 0;
      @include scrollAnimItem(0.4, ease, 0.4);

      &.anim-item--show {
        transform: initial;
        opacity: 1;
      }

      @include small-tablet {
        display: none;
      }
    }
  }

  &__container {
    position: relative;
    z-index: 1;
  }

  &__title {
    margin-bottom: 90px;
    max-width: 868px;

    @include scrollAnimItem(0.4, ease, 0.2);
    transform: translateY(40px);
    opacity: 0;

    &.anim-item--show {
      transform: initial;
      opacity: 1;

      &.solution__title--line {
        &::before {
          transition: width 0.7s ease 0.4s;
          width: 27%;

          @include mobile {
            width: 31%;
          }
        }
      }
    }

    &--line {
      &::before {
        top: 78%;
        right: 17px;
        width: 0;

        @include mobile {
          top: 50%;
          right: 3px;
          width: 0;
        }
      }
    }

    @include tablet {
      max-width: 555px;
    }

    @include mobile {
      margin: 0 -13px;
      margin-bottom: 52px;
      line-height: 52px;
      max-width: 345px;
    }
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(210px, 1fr));
    column-gap: clamp(1.25rem, -2.9038rem + 6.4904vw, 2.9375rem);
    row-gap: 35px;
    justify-items: center;

    @include mobile {
      grid-template-columns: repeat(2, 1fr);
      column-gap: 15px;
      row-gap: 20px;
    }
  }

  &__item {
    @include scrollAnimList(0.4, ease, 9, 0.2, 0.6);
    transform: scale(0.9);
    opacity: 0;

    &.anim-item--show {
      transform: initial;
      opacity: 1;
    }
  }
}
