.services {
  padding-top: 100px;
  overflow: hidden;

  @include small-tablet {
    padding-top: 44px;
  }

  @include mobile {
    padding-top: 61px;
  }

  &__container {
    display: flex;
    justify-content: space-between;
    padding-left: max(var(--container-offset), calc((100% - var(--content-width)) / 2));
    padding-right: max(var(--container-offset), calc((100% - 1530px) / 2));

    @include small-tablet {
      flex-wrap: wrap;
      gap: 40px;
      padding: 0 var(--container-offset);
    }

    @include mobile {
      padding: 0;
      gap: 30px;
    }
  }

  &__wrapper {
    margin-right: 20px;
    max-width: 444px;
    flex-basis: 30%;
    padding-top: 65px;
    transform: translateX(-60px);
    opacity: 0;

    @include scrollAnimItem(0.4, ease, 0.3);

    &.anim-item--show {
      transform: initial;
      opacity: 1;
    }

    @include tablet {
      padding-top: 30px;
    }

    @include small-tablet {
      flex-basis: auto;
      max-width: 100%;
      margin: 0;
    }

    @include mobile {
      padding: 0 28px;
    }
  }

  &__title {
    margin-bottom: 23px;
    line-height: 67px;

    @include tablet {
      line-height: 47px;
    }

    @include small-tablet {
      max-width: 276px;
    }
  }

  &__title-line {
    &::before {
      width: 55%;
      right: 0;

      @include mobile {
        width: 58%;
      }
    }
  }

  &__text {
    font-weight: 400;
    font-size: 16px;
    line-height: 26px;
    color: var(--color-gray);
    @include mb(26px);

    @include mobile {
      font-weight: 400;
      font-size: 14px;
      line-height: 24px;

      @include mb(23px);
    }
  }

  &__tabs {
    margin-right: -70px;
    flex-basis: 62.4%;

    transform: translateX(60px);
    opacity: 0;

    @include scrollAnimItem(0.4, ease, 0.3);

    &.anim-item--show {
      transform: initial;
      opacity: 1;
    }

    @include tablet {
      flex-basis: 69.4%;
    }

    @include small-tablet {
      flex-basis: auto;
      max-width: 100%;
    }
  }
}
