.footer {
  padding: 85px 0 82px;
  @include bg-cover;
  background-image: url(../img/footer-bg.svg);

  @include mobile {
    padding: 45px 0 82px;
  }

  &__container {
    display: flex;
    justify-content: space-between;
    gap: 20px;
    position: relative;
    z-index: 1;
    flex-wrap: wrap;
    transform: translateY(50px);
    opacity: 0;
    @include scrollAnimItem(0.4, ease, 0.3);

    &.anim-item--show {
      transform: initial;
      opacity: 1;
    }
  }

  &__wrapper {
    flex-basis: 35%;
    max-width: 406px;

    @include mobile {
      flex-basis: auto;
      max-width: 100%;
    }
  }

  &__logo {
    width: 352px;
    height: 109px;
    margin-bottom: 31px;

    @include tablet {
      width: 237px;
      height: 74px;
    }

    @include mobile {
      width: 212px;
      height: 65px;
      margin-bottom: 7px;
    }
  }

  &__text {
    font-weight: 400;
    font-size: 14px;
    line-height: 24px;
    color: var(--color-amulet);
    padding-bottom: 25px;
    border-bottom: 1px solid var(--color-light-green);
    margin-bottom: 36px;

    @include mobile {
      font-size: 13px;
      line-height: 23px;
    }
  }

  &__slogan {
    font-family: 'Ruberoid', sans-serif;
    font-weight: 400;
    font-size: clamp(0.9375rem, 0.476rem + 0.7212vw, 1.125rem);
    line-height: 24px;
    color: var(--color-gray-asparagus);
    display: block;

    @include tablet {
      max-width: 252px;
    }

    @include mobile {
      max-width: 100%;
    }
  }

  &__slogan-accent {
    font-size: clamp(1.25rem, 0.7885rem + 0.7212vw, 1.4375rem);
    line-height: 23px;
    color: var(--color-light-green);

    @include mobile {
      font-size: 18px;
      line-height: 37px;
    }
  }

  &__address {
    flex-basis: 20%;
    max-width: 232px;

    @include small-tablet {
      flex-basis: 44%;
      max-width: 341px;
    }

    @include mobile {
      flex-basis: auto;
      max-width: 100%;
      width: 100%;
    }
  }

  &__pt {
    padding-top: 20px;

    @include mobile {
      padding: 0;
    }
  }

  &__accent {
    font-family: 'Ruberoid', sans-serif;
    font-weight: 400;
    font-size: 26px;
    line-height: 35px;
    color: var(--color-gray-asparagus);
    display: block;
    margin-bottom: 28px;

    @include mobile {
      font-size: 22px;
      line-height: 30px;
      margin-bottom: 18px;
    }
  }

  &__address-list {
    @include mobile {
      display: grid;
      column-gap: 28px;
      row-gap: 16px;
      grid-template-columns: repeat(2, 1fr);
      border-bottom: 1px solid var(--color-pixie-green);
    }
  }


  &__address-item {
    @include mb(13px);
    padding-bottom: 14px;

    @include mobile {
      margin-bottom: 0 !important;
      padding: 0;
    }

    &--g-1 {
      @include mobile {
        grid-column: 1/2;
      }
    }

    &--g-2 {
      @include mobile {
        grid-column: 2/2;
        grid-row: 1/3;
      }
    }

    &--g-3 {
      @include mobile {
        grid-column: 2/2;
        grid-row: 2/3;
        margin-top: 21px;
      }
    }

    &--m {
      @include mobile {
        margin-top: -50px;
      }
    }
  }

  &__address-item--line {
    border-bottom: 1px solid var(--color-pixie-green);

    @include mobile {
      border: none;
    }
  }

  &__address-text {
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    color: var(--color-xanadu);
    display: block;
    padding-left: 36px;
    width: fit-content;
    position: relative;

    &::before {
      @include pseudo;
      position: absolute;
      @include bg-contain;
      top: 0;
      left: 0;
    }

    @include mobile {
      font-size: 13px;
      line-height: 16px;
      padding-left: 29px;
    }
  }

  &__address-text--phone {
    &::before {
      width: 22px;
      height: 24px;
      background-image: url(../img/phone-icon.png);

      @include mobile {
        width: 16px;
        height: 16px;
      }
    }
  }

  &__address-text--email {
    &::before {
      width: 24px;
      height: 18px;
      background-image: url(../img/email-icon.svg);

      @include mobile {
        width: 16px;
        height: 13px;
      }
    }
  }

  &__address-text--calendar {
    font-size: 14px;
    line-height: 20px;

    &::before {
      width: 22px;
      height: 22px;
      background-image: url(../img/calendar-iion.svg);

      @include mobile {
        width: 15px;
        height: 16px;
      }
    }

    @include mobile {
      font-size: 13px;
      line-height: 20px;
    }
  }

  &__address-text--geo {
    font-size: 14px;
    line-height: 20px;

    &::before {
      width: 24px;
      height: 33px;
      background-image: url(../img/geo-icon.svg);

      @include mobile {
        width: 16px;
        height: 20px;
      }
    }

    @include mobile {
      font-size: 13px;
      line-height: 20px;
    }
  }

  &__form {
    flex-basis: 25%;

    @include small-tablet {
      flex-basis: auto;
      margin: 0 auto;
      display: flex;
      flex-direction: column;
      max-width: 452px;
      width: 100%;
    }

    @include mobile {
      max-width: 100%;
    }
  }

  &__form-content {
    min-height: 119px;
    display: flex;
    flex-direction: column;
    margin-bottom: 34px;

    .just-validate-error-label {
      font-size: 12px;
      line-height: 15px;
    }
  }

  &__form-label {
    display: block;
    @include mb(auto);

    @include small-tablet {
      max-width: 100%;
    }
  }

  &__form-input {
    padding: 16px 22px;

    @include small-tablet {
      max-width: 100%;
    }
  }

  &__btn {
    @include small-tablet {
      margin-right: auto;
      margin-left: auto;
    }
  }
}
