.clients {
  padding: 152px 0 94px;
  border-bottom: 2px solid #7ca2814a;
  position: relative;

  @include mobile {
    background-color: var(--color-blue-romance);
    padding: 15px 0 46px;
  }

  &::before {
    @include pseudo;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;

    @include bg-cover;
    background-image: url(../img/clients-bg.svg);
    background-position: center 0;

    @include mobile {
      top: -38px;
      height: 38px;
    }
  }

  &__container {
    display: flex;
    gap: 35px;
    padding-left: max(var(--container-offset), (100% - var(--content-width)) / 2);
    position: relative;
    z-index: 1;

    @include tablet {
      justify-content: space-between;
    }

    @include mobile {
      flex-wrap: wrap;
      padding: 0 28px;
    }
  }

  &__wrapper {
    max-width: clamp(25.375rem, -9.3942rem + 54.3269vw, 39.5rem);
    flex-basis: 56%;

    @include small-tablet {
      flex-basis: 47%;
    }

    @include mobile {
      flex-basis: auto;
      max-width: 100%;
    }
  }

  &__title {
    font-size: clamp(1.75rem, -0.8654rem + 4.0865vw, 2.8125rem);
    line-height: clamp(2.75rem, -0.6346rem + 5.2885vw, 4.125rem);
    margin-bottom: 28px;
    transform: translateY(40px);
    opacity: 0;
    @include scrollAnimItem(0.4, ease, 0.3);

    &.anim-item--show {
      transform: initial;
      opacity: 1;

      .clients__title-line {
        &::before {
          transition: width 0.4s ease 0.5s;
          width: 17%;
        }
      }
    }

    @include small-tablet {
      font-size: 18px;
      line-height: 28px;
      max-width: 264px;
    }

    @include mobile {
      font-size: 28px;
      line-height: 38px;
      max-width: 100%;
    }
  }

  &__title-line {
    &--ds-none {
      @include mobile {
        display: none;
      }
    }

    &::before {
      right: 10px;

      @include mobile {
        display: none;
      }
    }
  }

  &__text {
    font-weight: 400;
    font-size: 16px;
    line-height: 26px;
    color: var(--color-gray);
    transform: translateY(40px);
    opacity: 0;
    @include scrollAnimItem(0.4, ease, 0.5);

    &.anim-item--show {
      transform: initial;
      opacity: 1;
    }

    @include mobile {
      font-size: 14px;
      line-height: 24px;
    }
  }

  &__slider {
    padding-top: 94px;
    padding-left: 26px;
    margin: 0;
    min-height: 268px;
    width: 56%;
    max-width: 839px;
    transform: translateX(40px);
    opacity: 0;
    @include scrollAnimItem(0.4, ease, 0.7);

    &.anim-item--show {
      transform: initial;
      opacity: 1;
    }

    &::before {
      @include pseudo;
      position: absolute;
      top: 0;
      left: 0;
      width: 26px;
      height: 100%;
      box-shadow: 34px 0px 26px -25px #D9FADD inset;

      @include mobile {
        display: none;
      }
    }

    @include tablet {
      width: 44%;
    }

    @include mobile {
      width: auto;
      padding: 0;
      margin-right: -28px;
      min-height: 190px;
    }
  }

  &__control {
    width: 28px;
    height: 28px;
    top: auto;
    bottom: 0;
    margin: 5px;

    &::after {
      width: calc(100% + (5px * 2));
      height: calc(100% + (5px * 2));
    }

    @include mobile {
      width: 34px;
      height: 34px;
    }

    &.swiper-button-prev {
      left: auto;
      right: 31%;

      @include tablet {
        right: 43%;
      }

      @include mobile {
        right: auto;
        left: 50%;
        transform: translateX(-218%);
      }
    }

    &.swiper-button-next {
      right: 21%;

      @include mobile {
        right: auto;
        left: 50%;
        transform: translateX(5%);
      }
    }
  }

  .swiper-slide {
    max-width: 258px;
    max-height: 116px;
    background-color: var(--color-white);
    border-radius: 30px;
    box-shadow: 0px 0px 10px #495d4c1a;
    overflow: hidden;

    @include flex-all-center;
  }

  .swiper-wrapper {
    z-index: -1;
  }
}
