.steps {
  padding: 56px 0 99px;

  @include mobile {
    padding: 30px 0 72px;
  }

  &__container {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    column-gap: 20px;
    row-gap: 90px;

    @include small-tablet {
      row-gap: 30px;
    }
  }

  &__title {
    flex-basis: 50%;
    line-height: clamp(2rem, -3.0769rem + 7.9327vw, 4.0625rem);
    max-width: clamp(20.9375rem, -15.8317rem + 57.4519vw, 32.875rem);
    font-size: clamp(1.75rem, -0.8654rem + 4.0865vw, 2.8125rem);
    transform: translateX(-40px);
    opacity: 0;
    @include scrollAnimItem(0.4, ease, 0.3);

    &.anim-item--show {
      transform: initial;
      opacity: 1;
    }

    @include tablet {
      flex-basis: auto;
    }

    @include mobile {
      line-height: 40px;
      margin: 0 -4px;
    }
  }

  &__title-line {
    &::before {
      right: 12px;
      width: 19%;

      @include mobile {
        right: 0;
      }
    }
  }

  &__sign {
    flex-basis: 48%;
    transform: translateX(40px);
    opacity: 0;
    @include scrollAnimItem(0.4, ease, 0.3);

    &.anim-item--show {
      transform: initial;
      opacity: 1;
    }

    @include small-tablet {
      flex-basis: auto;
      order: 1;
    }
  }

  &__sign-accent {
    font-family: 'Ruberoid', sans-serif;
    font-weight: 400;
    font-size: 26px;
    line-height: 35px;
    text-transform: capitalize;
    color: var(--color-light-green);
    display: block;
    margin-bottom: 17px;
  }

  &__sign-item {
    padding-left: 25px;
    @include bg-auto;
    background-position: left top;
    background-image: url(../img/petal-icon.svg);

    @include mb(10px);

    @include mobile {
      padding-left: 36px;
    }
  }

  &__sign-text {
    font-weight: 400;
    font-size: 16px;
    line-height: 19px;
    color: var(--color-amulet);

    @include mobile {
      font-size: 16px;
      line-height: 26px;
    }
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 20px;
    width: 100%;
    justify-items: center;

    @include mobile {
      grid-template-columns: 1fr;
      gap: 29px;
      margin-bottom: 12px;
    }
  }

  &__item {
    transform: scale(0.9);
    opacity: 0;

    @include scrollAnimList(0.4, ease, 4, 0.2, 0.6);

    &.anim-item--show {
      transform: initial;
      opacity: 1;
    }

    @include mobile {
      position: relative;
    }

    &--1 {
      @include mobile {
        left: -10%;
      }
    }

    &--2 {
      @include mobile {
        left: 12%;
      }
    }

    &--3 {
      @include mobile {
        left: -22%;
      }
    }
  }

  &__item-icon {
    background-color: var(--color-blue-romance);
    width: 100px;
    height: 100px;
    border-radius: 50%;
    margin: 0 auto;
    margin-bottom: 27px;

    @include bg-auto;

    @include mobile {
      margin-bottom: 16px;
    }
  }

  &__item-icon--1 {
    background-image: url(../img/bulb.svg);
  }

  &__item-text {
    font-weight: 600;
    font-size: 24px;
    line-height: 29px;
    color: var(--color-gray-asparagus);
    display: block;
    text-align: center;

    @include small-tablet {
      font-size: 23px;
    }
  }

  &__item-icon--2 {
    background-image: url(../img/journal.svg);
  }

  &__item-icon--3 {
    background-image: url(../img/blower.svg);
  }
}
