.scheme {
  padding: 0 0 50px;
  overflow: hidden;

  &__container {
    position: relative;
    z-index: 1;
  }

  &__title {
    margin-bottom: 100px;
    transform: translateY(40px);
    opacity: 0;

    @include scrollAnimItem(0.4, ease, 0.3);

    &.anim-item--show {
      transform: initial;
      opacity: 1;
    }

    @include mobile {
      margin-bottom: 23px;
    }
  }

  &__list {
    margin-bottom: 142px;

    @include mobile {
      margin-bottom: 27px;
    }
  }

  &__item--1 {
    margin-bottom: 12px;
    max-width: 46%;

    &::before {
      top: -83px;
      right: 65px;
      width: 309px;
      height: 306px;
      background-image: url(../img/envelope-bg.svg);

      @include mobile {
        top: -7px;
        right: -80px;
        width: 160px;
        height: 148px;
      }
    }

    &::after {
      right: -130px;
      width: 95%;
      height: 78px;
      background-image: url(../img/scheme-decor-path-1.svg);
    }

    @include mobile {
      margin-bottom: 27px;
    }
  }

  &__item-accent {
    font-family: 'Ruberoid', sans-serif;
    font-weight: 400;
    font-size: 35px;
    line-height: 47px;
    text-transform: capitalize;
    color: var(--color-light-green);
    display: block;
    margin-bottom: 20px;

    &--right {
      text-align: right;
    }

    @include tablet {
      font-size: 24px;
      line-height: 32px;
    }

    @include mobile {
      margin-bottom: 15px;
    }
  }

  &__item-desc {
    font-weight: 400;
    font-size: 16px;
    line-height: 26px;
    color: var(--color-gray-asparagus);
    display: block;

    &--right {
      text-align: right;
    }

    @include tablet {
      font-size: 14px;
      line-height: 24px;
    }
  }

  &__item--right {
    margin-left: auto;
  }

  &__item--2 {
    margin-bottom: 84px;
    max-width: 47%;

    &::before {
      top: -78px;
      left: 67px;
      width: 300px;
      height: 267px;
      background-image: url(../img/consultation-bg.svg);

      @include mobile {
        top: 4px;
        right: auto;
        left: -70px;
        width: 160px;
        height: 136px;
      }
    }

    &::after {
      bottom: -122px;
      left: -148px;
      width: 70%;
      height: 50px;
      background-image: url(../img/scheme-decor-path-2.svg);
    }

    @include mobile {
      margin-bottom: 27px;
    }
  }

  &__item--5 {
    margin-bottom: 78px;
    max-width: 48%;

    &::before {
      top: -64px;
      right: 162px;
      width: 282px;
      height: 325px;
      background-image: url(../img/treaty-bg.svg);

      @include mobile {
        top: -7px;
        right: -80px;
        width: 160px;
        height: 148px;
      }
    }

    &::after {
      bottom: -190px;
      right: -67px;
      width: 73%;
      height: 68px;
      background-image: url(../img/scheme-decor-path-5.svg);
    }

    @include mobile {
      margin-bottom: 28px;
    }
  }

  &__item {
    position: relative;
    transform: translateY(40px);
    opacity: 0;

    @include scrollAnimItem(0.4, ease, 0.3);

    &.anim-item--show {
      transform: initial;
      opacity: 1;
    }

    &::before,
    &::after {
      @include pseudo;
      position: absolute;
      z-index: -1;

      @include bg-contain;
    }

    &::after {
      @include tablet {
        display: none;
      }
    }

    @include tablet {
      max-width: 55%;
    }

    @include small-tablet {
      max-width: 43%;
    }

    @include mobile {
      max-width: 81%;
    }
  }

  &__item--3 {
    margin-bottom: 25px;
    max-width: 43%;

    &::before {
      top: -85px;
      left: 65px;
      width: 412px;
      height: 254px;
      background-image: url(../img/house-bg.svg);

      @include mobile {
        top: -33px;
        left: auto;
        right: -11px;
        width: 211px;
        height: 179px;
      }
    }

    &::after {
      bottom: -115px;
      left: 379px;
      width: 43%;
      height: 95px;
      background-image: url(../img/scheme-decor-path-3.svg);
    }

    @include mobile {
      max-width: 96%;
    }
  }

  &__item--4 {
    margin-bottom: 25px;
    max-width: 54%;

    &::before {
      top: -69px;
      left: 111px;
      width: 288px;
      height: 274px;
      background-image: url(../img/calendar-bg.svg);

      @include mobile {
        top: 13px;
        right: auto;
        left: -32px;
        width: 160px;
        height: 148px;
      }
    }

    &::after {
      bottom: -189px;
      left: -149px;
      width: 92%;
      height: 139px;
      background-image: url(../img/scheme-decor-path-4.svg);
    }

    @include mobile {
      margin-bottom: 28px;
      max-width: 91%;
    }
  }

  &__item--6 {
    max-width: 41%;

    &::before {
      top: -4px;
      left: -92px;
      width: 301px;
      height: 189px;
      background-image: url(../img/loader-bg.svg);

      @include mobile {
        top: -10px;
        right: auto;
        left: -27px;
        width: 160px;
        height: 148px;
      }
    }

    &::after {
      bottom: -90px;
      left: -28px;
      width: 70%;
      height: 37px;
      background-image: url(../img/scheme-decor-path-6.svg);
    }

    @include mobile {
      max-width: 90%;
    }
  }

  &__text {
    font-family: 'Ruberoid', sans-serif;
    font-weight: 400;
    font-size: 35px;
    line-height: 47px;
    text-transform: capitalize;
    color: var(--color-light-green);
    display: block;
    text-align: center;
    position: relative;
    transform: translateY(40px);
    opacity: 0;

    @include scrollAnimItem(0.4, ease, 0.6);

    &.anim-item--show {
      transform: initial;
      opacity: 1;
    }

    &::before {
      @include pseudo;
      position: absolute;
      z-index: -1;
      left: 50%;
      top: 93%;
      transform: translate(-50%, -50%);
      width: 402px;
      height: 99px;

      @include bg-contain;
      background-image: url(../img/handshake-bg.svg);

      @include mobile {
        width: 266px;
        transform: translate(-50%, -35%);
      }
    }

    @include tablet {
      font-size: 24px;
      line-height: 34px;
    }
  }
}
