.card {
  max-width: 257px;
  position: relative;
  overflow: hidden;
  border-radius: 25px;
  min-height: 135px;
  background-color: var(--color-white);
  box-shadow: 0px 2px 4px #2e23194d;

  @include mobile {
    min-height: 193px;
  }

  &__nameplate {
    position: absolute;
    z-index: 5;
    padding: 5px 16px;
    top: 14px;
    left: 0;
    font-weight: 600;
    font-size: 15px;
    line-height: 18px;
    color: var(--color-gray-asparagus);
  }

  &__picture {
    height: 92px;

    @include mobile {
      height: 131px;
    }
  }

  &__image {
    height: 100%;
    transition: transform var(--ui-transition) ease;

    @include hover {
      transform: scale(1.1);
    }
  }

  &__content {
    background-color: var(--color-white);
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    padding: 7px 15px 3px;
    min-height: 43px;
    display: flex;
    align-items: center;

    @include mobile {
      min-height: 63px;
      padding: 7px 15px 14px;
    }
  }

  &__content-text {
    margin: 0;
    font-weight: 400;
    font-size: 18px;
    line-height: 21px;
    color: var(--color-light-green);

    @include mobile {
      font-size: 16px;
      line-height: 19px;
    }
  }
}
