.hero {
  padding: 159px 0 64px;
  background: linear-gradient(180deg, #F1F1F1 0%, #F0F2F1 16.25%, #EBEDEC 43.26%, #E6E8E7 72.5%, #DEE2E1 100%);
  position: relative;
  overflow: hidden;

  &.anim-item--show {
    &::before {
      transform: initial;
      opacity: 1;

      @include mobile {
        transform: rotate(90deg) scale(195%);
      }
    }
  }

  @include mobile {
    padding: 144px 0 296px;
    background: linear-gradient(275deg, #F1F1F1 0%, #F0F2F1 16.25%, #EBEDEC 43.26%, #E6E8E7 72.5%, #DEE2E1 100%);
  }

  &::before {
    @include pseudo;
    position: absolute;
    top: 0;
    left: 50%;
    transform: translateX(-4.8%);
    height: 100%;
    width: 1557px;
    @include bg-cover;
    @include image-set('../img/hero-image');
    @include scrollAnimItem(0.4, ease, 1.2);

    transform: translateX(80px);
    opacity: 0;

    @include mobile {
      background-size: contain;
      transform: rotate(90deg) scale(195%);
      width: 100%;
      height: 375px;
      top: auto;
      bottom: -189px;
      left: 0;
    }
  }

  &__container {
    position: relative;
    z-index: 1;
  }

  &__wrapper {
    max-width: 523px;

    @include small-tablet {
      max-width: 458px;
    }

    @include mobile {}
  }

  &__title {
    margin-bottom: 34px;
    transform: translateY(60px);
    opacity: 0;

    @include scrollAnimItem(0.4, ease, 0.4);

    &.anim-item--show {
      transform: initial;
      opacity: 1;

      .hero__title-line {
        &::before {
          transition: width 0.7s ease 0.7s;
          width: 28%;

          @include mobile {
            width: 32%;
          }
        }
      }
    }

    @include mobile {
      max-width: 294px;
      margin-left: 13px;
    }
  }

  &__title-uppercase {
    text-transform: uppercase;
    color: var(--color-light-green);
  }

  &__subtile {
    font-weight: 400;
    font-size: 22px;
    line-height: 32px;
    color: var(--color-amulet);
    display: block;
    padding-left: 62px;
    margin-bottom: 52px;

    @include scrollAnimItem(0.4, ease, 0.6);

    transform: translateY(40px);
    opacity: 0;

    &.anim-item--show {
      transform: initial;
      opacity: 1;
    }

    @include bg-auto;
    background: url(../img/hero-decor-1.svg) left 10px no-repeat,
    url(../img/hero-decor-2.svg) 19px 10px no-repeat,
    url(../img/hero-decor-3.svg) 40px 10px no-repeat;

    @include small-tablet {
      font-size: 20px;
    }

    @include mobile {
      font-size: 18px;
      line-height: 28px;
      margin-bottom: 47px;
    }
  }

  &__title-line {
    text-align: right;

    &::before {
      width: 0;

      @include mobile {
        width: 0;
      }
    }
  }

  &__address {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 20px;
    margin-bottom: 62px;
    @include scrollAnimItem(0.4, ease, 0.8);

    transform: translateY(40px);
    opacity: 0;

    &.anim-item--show {
      transform: initial;
      opacity: 1;
    }

    @include mobile {
      grid-template-columns: 1fr;
      gap: 32px;
      margin-bottom: 46px;
    }
  }

  &__address-text {
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    color: var(--color-light-green);

    @include mobile {
      font-size: 14px;
      line-height: 17px;
    }
  }

  &__address-text--geo {
    display: block;
    padding-left: 66px;
    position: relative;

    @include mobile {
      padding-left: 32px;
    }

    &::before {
      @include pseudo;
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      left: 18px;
      @include bg-contain;
      background-image: url(../img/geo-icon.svg);
      width: 24px;
      height: 32px;

      @include mobile {
        width: 15px;
        height: 20px;
        left: 0;
      }
    }
  }

  &__address-list {
    padding-left: 65px;

    .hero__address-phone {
      &::before {
        display: none;
      }
    }

    @include mobile {
      padding: 0;
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      gap: 40px;

      &.hero__address-phone {
        &::before {
          display: none;
        }
      }

      .hero__address-phone {

        &::before {
          display: block;
          left: -13px;
        }
      }
    }
  }

  &__address-phone {
    position: relative;

    @include mobile {
      padding-left: 12px;
    }

    &::before {
      @include pseudo;
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      left: 21px;
      @include bg-contain;
      background-image: url(../img/phone-icon.png);
      width: 32px;
      height: 32px;

      @include mobile {
        width: 20px;
        height: 20px;
      }
    }
  }

  &__btn {
    transition: transform 0.4s ease 1s, opacity 0.4s ease 1s, background-color var(--ui-transition) ease;

    transform: translateY(40px);
    opacity: 0;

    &.anim-item--show {
      transform: initial;
      opacity: 1;
    }
  }
}
