// если вы хотите использовать sass-переменные - удалите root
// colors
:root {
  // base
  --font-family: "ProximaNova", sans-serif;
  --content-width: 1170px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));
  --ui-transition: 0.3s;

  // colors
  --color-white: #fff;
  --color-gray-asparagus: #495E4C;
  --color-green: #5CCD6A;
  --color-light-green: #78C081;
  --color-amulet: #7CA381;
  --color-blue-romance: #D9FADD;
  --color-gray: #848484;
  --color-xanadu: #7B897D;
  --color-pixie-green: #BEE1C2;
  --color-pastel-green: #54E165;
  --color-emerald: #3BCD4C;
  --color-persimmon: #FF5858;
  --color-moss-green: #9CD3A3;
}
