.about {
  padding-top: 100px;
  overflow: hidden;

  @include mobile {
    padding-top: 70px;
  }

  &__container {
    display: flex;
    justify-content: space-between;
    position: relative;
    z-index: 1;
    gap: 20px;

    @include small-tablet {
      flex-direction: column;
      gap: 40px;
    }

    @include mobile {
      gap: 28px;
    }
  }

  &__wrapper {
    flex-basis: 46%;
    max-width: 531px;
    padding: 28px 43px 33px;
    background-color: var(--color-white);
    box-shadow: 0px 0px 10px #495d4c1a;
    border-radius: 50px;
    position: relative;
    transform: translateX(-50px);
    opacity: 0;

    @include scrollAnimItem(0.4, ease, 0.3);

    &.anim-item--show {
      transform: initial;
      opacity: 1;

      &::before {
        transition: opacity 0.4s ease 0.8s;
        opacity: 1;
      }
    }

    &::before {
      @include pseudo;
      position: absolute;
      top: 0;
      left: -126px;
      width: 656px;
      height: 499px;
      border-radius: 50%;
      background-color: #baffc380;
      z-index: -1;
      filter: blur(100px);
      opacity: 0;
    }

    @include small-tablet {
      padding: 28px 25px 25px;
      max-width: 100%;
    }
  }

  &__title {
    margin-bottom: 21px;
    line-height: 67px;

    @include tablet {
      line-height: 44px;
    }

    @include mobile {
      margin-bottom: 24px;
    }
  }

  &__title-line {
    position: relative;
    display: block;
    max-width: 324px;

    &::before {
      @include pseudo;
      position: absolute;
      right: 0;
      top: 15px;
      content: 'Pascal vent';
      font-family: 'Ruberoid', sans-serif;
      font-weight: 400;
      font-size: 21px;
      line-height: 28px;
      text-transform: uppercase;
      color: var(--color-light-green);
      line-height: 31px;
      border-bottom: 1px solid var(--color-light-green);

      @include tablet {
        font-size: 15px;
        line-height: 22px;
        top: 11px;
      }
    }

    @include tablet {
      max-width: 217px;
    }
  }

  &__text {
    font-weight: 400;
    font-size: 16px;
    line-height: 26px;
    color: var(--color-gray);

    @include mb(26px);

    @include mobile {
      font-size: 14px;
      line-height: 24px;
      @include mb(24px);
    }
  }

  &__list {
    flex-basis: 49%;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 15px;
    row-gap: 90px;
    align-self: flex-start;
    padding-top: 119px;

    @include tablet {
      flex-basis: 52%;
    }

    @include small-tablet {
      padding: 0;
      flex-basis: auto;
      width: 100%;
    }

    @include mobile {
      grid-template-columns: 1fr;
      row-gap: 27px;
    }
  }

  &__item {
    position: relative;
    transform: scale(0.9);
    opacity: 0;

    @include scrollAnimList(0.4, ease, 5, 0.2, 0.5);

    &.anim-item--show {
      transform: initial;
      opacity: 1;
    }

    &::before {
      @include pseudo;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 122px;
      height: 122px;
      border-radius: 50%;
      background-color: #baffc380;
      filter: blur(50px);
    }

    @include mobile {
      width: fit-content;
    }

    &--1 {
      @include mobile {
        left: 0;
      }
    }

    &--2 {
      @include mobile {
        right: -34%;
      }
    }

    &--3 {
      @include mobile {
        left: 0;
      }
    }

    &--4 {
      @include mobile {
        right: -28%;
      }
    }
  }

  &__item-accent {
    font-family: 'Ruberoid', sans-serif;
    font-weight: 400;
    font-size: 24px;
    line-height: 35px;
    color: var(--color-light-green);
    display: block;
    text-align: center;
    margin-bottom: 19px;

    @include small-tablet {
      font-size: 16px;
    }

    @include mobile {
      font-size: 24px;
    }
  }

  &__item-accent-more {
    font-size: 35px;
    line-height: 47px;

    @include small-tablet {
      font-size: 24px;
    }

    @include mobile {
      font-size: 35px;
    }
  }

  &__item-text {
    font-weight: 400;
    font-size: 16px;
    line-height: 26px;
    color: var(--color-amulet);
    display: block;
    text-align: center;
    max-width: 192px;
    margin: 0 auto;
  }
}
