.advantages {
  padding: 100px 0 100px;
  overflow: hidden;

  @include mobile {
    padding: 17px 0 70px;
  }

  &__container {
    display: flex;
    gap: 93px;
    position: relative;
    z-index: 1;

    @include tablet {
      gap: 41px;
      align-items: center;
    }
  }

  &__left {
    flex-basis: 50%;
    max-width: 502px;
    padding-top: 51px;

    @include small-tablet {
      max-width: 100%;
      flex-basis: auto;
      display: flex;
      flex-direction: column;
    }
  }

  &__title {
    margin-bottom: 33px;
    transform: translateX(-40px);
    opacity: 0;

    @include scrollAnimItem(0.4, ease, 0.3);

    &.anim-item--show {
      transform: initial;
      opacity: 1;
    }
  }

  &__icons {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    gap: 8px;
    margin-bottom: 32px;

    @include tablet {
      grid-template-columns: repeat(4, 1fr);
      column-gap: 13px;
      row-gap: 15px;
    }

    @include small-tablet {
      grid-template-columns: repeat(6, 1fr);
      max-width: 486px;
    }

    @include mobile {
      grid-template-columns: repeat(4, 1fr);
      margin-bottom: 22px;
    }
  }

  &__icon {
    border: 1px solid var(--color-light-green);
    border-radius: 20px;
    width: 70px;
    height: 40px;
    transform: translateX(-40px);
    opacity: 0;

    @include scrollAnimList(0.4, ease, 7, 0.1, 0.5);

    &.anim-item--show {
      transform: initial;
      opacity: 1;
    }

    @include bg-auto;
  }

  &__icon--1 {
    background-image: url(../img/advantages-icon-1.svg);
  }

  &__icon--2 {
    background-image: url(../img/advantages-icon-2.svg);
  }

  &__icon--3 {
    background-image: url(../img/advantages-icon-3.svg);
  }

  &__icon--4 {
    background-image: url(../img/advantages-icon-4.svg);
  }

  &__icon--5 {
    background-image: url(../img/advantages-icon-5.svg);
  }

  &__icon--6 {
    background-image: url(../img/advantages-icon-6.svg);
  }

  &__list {
    max-width: 455px;
    margin-bottom: 25px;

    @include small-tablet {
      max-width: 100%;
    }

    @include mobile {
      margin-bottom: 11px;
    }
  }

  &__item {
    padding-left: 25px;
    position: relative;
    transform: translateX(-40px);
    opacity: 0;

    @include scrollAnimList(0.4, ease, 7, 0.1, 0.7);

    &.anim-item--show {
      transform: initial;
      opacity: 1;
    }

    @include mb(11px);

    &::before {
      @include pseudo;
      position: absolute;
      top: 5px;
      left: 0;
      width: 15px;
      height: 15px;
      border: 1px solid var(--color-light-green);
      border-radius: 50%;

      @include bg-auto;
      background-image: url(../img/dot-icon.svg);
    }

    @include mobile {
      @include mb(17px);
    }
  }

  &__item-text {
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    color: var(--color-light-green);
  }

  &__text {
    font-weight: 400;
    font-size: 16px;
    line-height: 26px;
    color: var(--color-gray);
    margin-bottom: 49px;
    transform: translateX(-40px);
    opacity: 0;

    @include scrollAnimItem(0.4, ease, 1.2);

    &.anim-item--show {
      transform: initial;
      opacity: 1;
    }

    @include mobile {
      font-size: 14px;
      line-height: 24px;
      margin-bottom: 45px;
    }
  }

  &__btn {
    padding: 32px 20px;
    max-width: 447px;
    width: 100%;
    transform: translateX(-40px);
    opacity: 0;
    transition: transform 0.4s ease 1.4s, opacity 0.4s ease 1.4s, background-color var(--ui-transition) ease;

    &.anim-item--show {
      transform: initial;
      opacity: 1;
    }

    @include small-tablet {
      margin-left: auto;
      margin-right: auto;
    }

    @include mobile {
      padding: 18px 20px;
      max-width: 289px;
    }
  }

  &__right {
    flex-basis: 50%;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    align-items: center;
    column-gap: 34px;
    row-gap: 22px;
    position: relative;

    &::before {
      @include pseudo;
      position: absolute;
      top: 54px;
      z-index: -1;
      width: 718px;
      height: 718px;
      border-radius: 50%;
      background-color: #BAFFC3;
      filter: blur(175px);

      @include tablet {
        width: 641px;
        height: 563px;
      }
    }

    @include small-tablet {
      display: none;
    }
  }

  &__picture {
    overflow: hidden;
    border-radius: 39px;
    box-shadow: 0px 0px 10px #1a1a1a4d;
    justify-self: flex-start;
    transform: scale(0.9);
    opacity: 0;

    @include scrollAnimList(0.4, ease, 5, 0.1, 2);

    &.anim-item--show {
      transform: initial;
      opacity: 1;
    }
  }

  &__picture--2 {
    position: relative;
    top: 63px;

    @include tablet {
      top: 41px;
    }
  }
}
