.header {
  padding: 19px 0;
  position: absolute;
  z-index: 5;
  width: 100%;
  transform: translateY(-50px);
  opacity: 0;

  @include scrollAnimItem(0.4, ease, 0.2);

  &.anim-item--show {
    opacity: 1;
    transform: initial;
  }

  &.fixed-block:not(.header--fixed) {
    padding-right: 0 !important;
  }

  &--fixed {
    position: fixed;
    left: 0;
    top: 0;
    transition: transform var(--ui-transition) ease;
    background-color: var(--color-blue-romance);
    border-radius: 0px 0px 70px 70px;

    @include tablet {
      border-radius: 0px 0px 40px 40px;
    }
  }

  @include mobile {
    padding: 14px 0 14px;
  }

  &__container {
    @include flex-all-sb;
    max-width: 1380px;
  }

  &__logo {
    @include mobile {
      width: 168px;
      height: 53px;
    }
  }

  &__btn {
    padding: 17px 41px;

    @include tablet {
      margin-left: auto;
    }
  }

  &__burger {
    display: none;

    @include tablet {
      display: block;
      z-index: 15;
    }
  }
}
