.modal {
  background-color: #d9fadd8c;
  backdrop-filter: blur(10px);
  min-width: 375px;

  &::before {
    display: none;
  }

  &__connection {
    border-radius: 50px;
    background-color: var(--color-blue-romance);
    box-shadow: 0px 0px 10px #495d4c1a;
    width: 100%;
    height: auto;
    max-width: 445px;
    margin: 179px 20px 20px;
    padding: 33px;
    padding-bottom: 0;

    @include mobile {
      width: 89%;
    }

    &--p {
      padding-bottom: 17px;
    }
  }

  &__head {
    position: relative;
    margin-bottom: 22px;
  }

  &__title {
    font-size: 40px;
    line-height: 55px;
    width: fit-content;

    @include tablet {
      font-size: 28px;
      line-height: 38px;
    }
  }

  &__title-line {
    &::before {
      right: 0;
      width: 43%;
    }
  }

  &__btn-close {
    position: absolute;
    top: -117px;
    right: -34px;
  }

  &__main {
    border-radius: 35px 35px 50px 50px;
    background-color: var(--color-white);
    box-shadow: 0px 0px 10px #495d4c1a;
    margin: 0 -33px;
    padding: 38px 34px;
  }

  &__text {
    font-weight: 400;
    font-size: 16px;
    line-height: 26px;
    color: var(--color-amulet);
    margin-bottom: 20px;
  }

  &__calls-accent {
    font-weight: 600;
    font-size: 16px;
    line-height: 19px;
    color: var(--color-amulet);
    display: block;
    margin-bottom: 17px;
  }

  &__calls-time {
    font-weight: 600;
    font-size: 16px;
    line-height: 19px;
    color: var(--color-amulet);
    display: block;
    margin-bottom: 45px;
  }

  &__num-list {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 15px;
  }

  &__num-item {
    &:not(:last-child) {
      border-right: 1px solid var(--color-pixie-green);

      @include mobile {
        border-right: none;
      }
    }
  }

  &__num-text {
    font-weight: 600;
    font-size: 16px;
    line-height: 24px;
    color: var(--color-xanadu);
    padding-left: 30px;
    padding-right: 20px;
    position: relative;

    &::before {
      @include pseudo;
      position: absolute;
      top: 50%;
      left: 0;
      transform: translateY(-50%);
      @include bg-contain;
      width: 22px;
      height: 22px;
      background-image: url(../img/phone-icon.png);

      @include mobile {
        width: 15px;
        height: 15px;
      }
    }

    @include mobile {
      font-size: 12px;
      padding-left: 27px;
      padding-right: 0px;
    }
  }

  &__form {
    padding: 35px;
    display: flex;
    flex-direction: column;
    margin-bottom: 15px;
  }

  &__form-content {
    margin-bottom: 24px;

    .just-validate-error-label {
      font-weight: 400;
      font-size: 12px;
      line-height: 15px;
    }
  }

  &__label {
    display: block;
    min-height: 70px;
    @include mb(10px);
  }

  &__input {
    padding: 16px 20px;
    max-width: 100%;
    margin-bottom: 5px;
  }

  &__btn {
    padding: 19px 0;
    margin: 10px;
    font-weight: 400;
    font-size: 15px;
    line-height: 21px;
    border-radius: 17px;

    &::before {
      border-radius: 20px;
      top: -10px;
      left: -10px;
      width: calc(100% + (10px *2));
      height: calc(100% + (10px *2));
    }
  }

  &__logo {
    margin: 0 auto;
    width: 228px;
    height: 68px;

    @include mobile {
      width: 190px;
      height: 54px;
    }
  }
}
