.portfolio {
  padding: 92px 0 100px;
  overflow: hidden;

  @include mobile {
    padding: 61px 0 108px;
  }

  &__container {
    display: flex;
    column-gap: 27px;
    justify-content: space-between;
    margin-bottom: 45px;

    @include mobile {
      flex-wrap: wrap;
      margin-bottom: 30px;
    }
  }

  &__title {
    flex-basis: 43%;
    @include scrollAnimItem(0.4, ease, 0.3);

    transform: translateX(-40px);
    opacity: 0;

    &.anim-item--show {
      transform: initial;
      opacity: 1;
    }

    @include mobile {
      flex-basis: auto;
      margin-bottom: 21px;
    }
  }

  &__text {
    font-weight: 400;
    font-size: 16px;
    line-height: 26px;
    color: var(--color-gray);
    flex-basis: 55%;
    padding-top: 10px;
    @include scrollAnimItem(0.4, ease, 0.3);

    transform: translateX(40px);
    opacity: 0;

    &.anim-item--show {
      transform: initial;
      opacity: 1;
    }

    @include mobile {
      flex-basis: auto;
      font-size: 14px;
      line-height: 24px;
    }
  }

  &__wrapper {
    padding-left: max(var(--container-offset), (100% - var(--content-width)) / 2);
    @include scrollAnimItem(0.4, ease, 0.7);

    transform: translateY(40px);
    opacity: 0;

    &.anim-item--show {
      transform: initial;
      opacity: 1;
    }

    @include mobile {
      padding: 0 28px;
    }
  }

  &__slider {
    padding-top: 70px;
    overflow: initial;

    &::before {
      @include pseudo;
      z-index: -1;
      width: 556px;
      height: 499px;
      top: -76px;
      left: -98px;
      border-radius: 50%;
      position: absolute;
      background: #baffc380;
      filter: blur(100px);
    }

    @include mobile {
      display: flex;
      flex-direction: column;
      padding: 0;
      padding-bottom: 75px;
    }
  }

  &__control {
    width: 34px;
    height: 34px;
    top: 0;
    margin: 5px;

    &::after {
      top: -5px;
      left: -5px;
      width: calc(100% + (5px * 2));
      height: calc(100% + (5px * 2));
    }

    &.swiper-button-prev {
      left: 0;

      @include mobile {
        left: 50%;
        transform: translateX(-192%);
      }
    }

    &.swiper-button-next {
      right: auto;
      left: 71px;

      @include mobile {
        left: auto;
        right: 50%;
        transform: translateX(174%);
      }
    }

    @include mobile {
      top: auto;
      bottom: 0;
    }
  }

  &__nameplate {
    position: absolute;
    top: 36px;
    left: 0;
    padding: 10px 35px;
    border-radius: 0px 15px 15px 0px;
  }

  &__logo {
    width: 94px;
    height: 29px;
  }

  &__picture {
    height: 456px;
    overflow: hidden;
    border-radius: 35px;

    @include mobile {
      height: 420px;
    }
  }

  &__image {
    width: 100%;
    height: 100%;
  }

  &__content {
    padding-bottom: 44px;
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    transition: transform var(--ui-transition) ease;
    transform: translateY(66px);

    @include mobile {
      padding-bottom: 36px;
    }
  }

  &__content-accent {
    font-family: 'Ruberoid', sans-serif;
    font-weight: 400;
    font-size: 24px;
    line-height: 34px;
    text-transform: capitalize;
    color: var(--color-white);
    display: block;
    padding: 0 40px;
    margin-bottom: 13px;

    @include mobile {
      padding: 0 31px;
      font-size: 22px;
      line-height: 32px;
      margin-bottom: 18px;
    }
  }

  &__content-desc {
    font-weight: 400;
    font-size: 16px;
    line-height: 19px;
    color: var(--color-white);
    display: block;
    padding: 0 40px;
    transition: opacity var(--ui-transition) ease var(--ui-transition), transform var(--ui-transition) ease var(--ui-transition);
    opacity: 0;
    transform: translateY(30px);

    @include mobile {
      line-height: 26px;
      padding: 0 30px;
    }
  }

  .swiper-slide {
    transition: max-width var(--ui-transition) ease;
    max-width: 270px;

    &.swiper-slide-active {
      max-width: 500px;

      .portfolio__content {
        transform: initial;
      }

      .portfolio__content-desc {
        opacity: 1;
        transform: initial;
      }

      @include mobile {
        max-width: initial;
      }
    }

    @include mobile {
      max-width: initial;
    }
  }
}
