.application {
  padding: 51px 0 49px;
  background-color: var(--color-blue-romance);
  @include bg-cover;
  background-image: url(../img/application-bg.svg);
  position: relative;

  @include mobile {
    padding: 32px 0 36px;
    background-image: none;
  }

  &__decor-item {
    width: 209px;
    height: 236px;
    right: 0;
    top: 10px;
    @include bg-contain;
    background-image: url(../img/application-decor.svg);
    transform: translateX(30px) translateY(-50%);
    opacity: 0;

    @include scrollAnimItem(0.4, ease, 0.7);

    &.anim-item--show {
      transform: translateX(0px) translateY(-50%);
      opacity: 1;
    }

    @include small-tablet {
      top: 50%;
      transform: translateY(-50%);
    }

    @include mobile {
      display: none;
    }
  }

  &__container {
    position: relative;
    z-index: 1;
  }

  &__title {
    font-family: 'Ruberoid', sans-serif;
    font-weight: 400;
    font-size: 35px;
    line-height: 45px;
    color: var(--color-gray-asparagus);
    margin-bottom: 28px;
    transform: translateY(30px);
    opacity: 0;

    @include scrollAnimItem(0.4, ease, 0.3);

    &.anim-item--show {
      transform: initial;
      opacity: 1;
    }

    @include mobile {
      font-size: 28px;
      line-height: 38px;
      letter-spacing: -1px;
      margin-bottom: 33px;
    }
  }

  &__form {
    display: flex;
    transform: translateY(30px);
    opacity: 0;

    @include scrollAnimItem(0.4, ease, 0.5);

    &.anim-item--show {
      transform: initial;
      opacity: 1;
    }

    @include small-tablet {
      flex-direction: column;
      align-items: center;
    }
  }

  &__form-content {
    display: flex;
    align-items: center;
    width: 100%;
    max-width: 610px;
    margin-right: 60px;
    position: relative;

    .just-validate-error-label {
      position: absolute;
      bottom: -15px;
      font-size: 12px;
      line-height: 15px;
    }

    @include small-tablet {
      flex-direction: column;
      max-width: 100%;
      margin: 0;
      margin-bottom: 45px;
    }
  }

  &__form-label {
    display: block;
    flex-basis: 100%;
    @include mr(30px);

    @include small-tablet {
      @include mr(0);
      @include mb(25px);
      width: 100%;
      max-width: 70%;
    }

    @include mobile {
      max-width: 100%;
    }
  }

  &__form-input {
    @include small-tablet {
      max-width: 100%;
      width: 100%;
    }
  }
}
