.h1 {
  font-family: 'Ruberoid', sans-serif;
  font-weight: 400;
  font-size: 64px;
  line-height: 74px;
  text-transform: capitalize;
  color: var(--color-gray-asparagus);

  @include small-tablet {
    font-size: 56px;
  }

  @include mobile {
    font-size: 36px;
    line-height: 49px;
  }
}

.h2 {
  font-family: 'Ruberoid', sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 45px;
  line-height: 60px;
  text-transform: capitalize;
  color: var(--color-gray-asparagus);

  @include tablet {
    font-size: 28px;
    line-height: 38px;
  }
}

.title {
  margin: 0;
}

.text {
  margin: 0;
}
