.logo {
  @include bg-contain;
  display: block;
  width: 196px;
  height: 61px;
  background-image: url(../img/logo.svg);
}

.btn-green {
  font-family: 'Ruberoid', sans-serif;
  font-weight: 400;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  color: var(--color-white);
  position: relative;
  background-color: var(--color-green);
  box-shadow: inset 2px -2px 12px #226f215c;
  transition: background-color var(--ui-transition) ease;

  &::before {
    @include pseudo;
    position: absolute;
    z-index: -1;
  }

  @include hover {
    background-color: var(--color-pastel-green);
  }

  &:active {
    background-color: var(--color-emerald);
  }

  &--min {
    border-radius: 17px;
    padding: 17px 50px;
    margin: 10px;
    font-size: 15px;
    line-height: 21px;

    @include mobile {
      padding: 17px 94px;
    }

    &::before {
      width: calc(100% + (10px * 2));
      height: calc(100% + (10px * 2));
      background-color: var(--color-white);
      opacity: 0.45;
      border-radius: 20px;
      top: -10px;
      left: -10px;
    }
  }

  &--max {
    border-radius: 30px;
    padding: 31px 125px;
    margin: 15px;
    font-size: 17px;
    line-height: 23px;
    z-index: 1;

    @include tablet {
      padding: 31px 115px;
    }

    @include mobile {
      padding: 19px 50px;
      border-radius: 20px;
      letter-spacing: initial;
    }

    &::before {
      width: calc(100% + (15px * 2));
      height: calc(100% + (15px * 2));
      background-color: #78c08140;
      border-radius: 40px;
      top: -15px;
      left: -15px;

      @include mobile {
        border-radius: 30px;
      }
    }
  }

  &.disabled {
    background-color: var(--color-moss-green);
    pointer-events: none;
  }
}

.line-decor {
  display: block;
  position: relative;

  &::before {
    @include pseudo;
    position: absolute;
    background-color: var(--color-light-green);
    height: 3px;
    border-radius: 8px;
    top: 50%;
    transform: translateY(-50%);
  }
}

.nameplate {
  display: block;
  border-radius: 0px 10px 10px 0px;
  background-color: var(--color-blue-romance);
}

.input {
  max-width: 290px;
  display: block;
  width: 100%;
  padding: 19px 20px;
  background-color: var(--color-white);
  box-shadow: 0px 0px 12px #495d4c26;
  border-radius: 15px;
  font-weight: 400;
  font-size: 14px;
  line-height: 17px;
  color: var(--color-amulet);

  &::placeholder {
    font-weight: 400;
    font-size: 14px;
    line-height: 17px;
    color: var(--color-amulet);
  }
}

.control {
  background-color: var(--color-green);
  border-radius: 50%;
  box-shadow: inset 2px -2px 12px #226f215c;
  transition: opacity var(--ui-transition) ease;

  &::after {
    @include pseudo;
    position: absolute;
    background-color: var(--color-light-green);
    opacity: 0.25;
    border-radius: 50%;
  }

  &__arr-icon {
    height: 100%;
    fill: var(--color-white);
  }

  &.swiper-button-lock {
    display: block;
  }
}

.burger {
  @include burger;
  background-color: var(--color-white);
  box-shadow: 0px 4px 10px #495d4c26;
  border-radius: 15px;
  --burger-width: 49px;
  --burger-height: 49px;
  color: var(--color-green);
  margin: 10px;

  &__line,
  &__line:first-child,
  &__line:last-child {
    width: 25px;
    left: 50%;
    transform: translateX(-50%);
    pointer-events: none;
  }

  &__line:first-child {
    top: 17px;
  }

  &__line:nth-child(2) {
    width: 6px;
    left: 31%;

    &::before {
      @include pseudo;
      position: absolute;
      right: -19px;
      width: 16px;
      height: 2px;
      background-color: inherit;
      border-radius: 15px;
    }
  }

  &__line:last-child {
    top: calc(calc(100% - 16px) - var(--burger-line-height));
  }

  &::before {
    @include pseudo;
    position: absolute;
    background: #ffffff73;
    border-radius: 20px;
    width: calc(100% + (10px * 2));
    height: calc(100% + (10px * 2));
    top: -10px;
    left: -10px;
  }

  &--active {

    .burger__line,
    .burger__line:first-child,
    .burger__line:last-child {
      left: 24%;
    }
  }
}

.btn-close {
  width: 53px;
  height: 49px;
  background-color: var(--color-green);
  box-shadow: inset 2px -2px 12px #226f215c;
  border-radius: 15px;
  margin: 10px;
  transition: background-color var(--ui-transition) ease;

  @include bg-auto;
  background-image: url(../img/close-icon.svg);
  position: relative;

  @include hover {
    background-color: var(--color-pastel-green);
  }

  &:active {
    background-color: var(--color-emerald);
  }

  &::before {
    @include pseudo;
    position: absolute;
    top: -10px;
    left: -10px;
    width: calc(100% + (10px * 2));
    height: calc(100% + (10px * 2));
    background-color: var(--color-light-green);
    opacity: 0.25;
    border-radius: 18px;
  }
}
