.nav {
  @include tablet {
    position: fixed;
    top: 0;
    right: 0;
    padding: 50px 85px 20px 20px;
    background-color: var(--color-blue-romance);
    width: 43%;
    height: 100%;
    overflow: auto;
    z-index: 10;
    display: flex;
    flex-direction: column;
    transition: transform var(--ui-transition) ease;
    transform: translateX(100%);

    @include hide-scallbar;

    &.menu--active {
      transform: initial;
    }
  }

  @include small-tablet {
    width: 61%;
  }

  @include mobile {
    width: 254px;
    padding-right: 23px;
    border-radius: 30px 0 0 30px;
  }

  &__list {
    display: flex;

    @include tablet {
      flex-direction: column;
      align-items: flex-end;
      margin-bottom: 30px
    }

    @include mobile {
      align-items: flex-start;
    }
  }

  &__item {
    @include mr(clamp(1.25rem, -1.8269rem + 4.8077vw, 2.5rem));

    @include tablet {
      @include mr(0);

      @include mb(20px);
    }
  }

  &__btn {
    font-weight: 400;
    font-size: 15px;
    line-height: 18px;
    text-transform: lowercase;
    color: var(--color-gray-asparagus);
    transition: color var(--ui-transition) ease;

    @include hover {
      color: var(--color-pastel-green);
    }

    @include tablet {
      font-size: 20px;
    }
  }
}
